import { useDispatch, useSelector } from "react-redux";
import { SET_VALUES, SET_ERRORS, SET_VALID } from "../services/actions/Auth";
import { useCallback } from "react";

export function useForm() {
  const dispatch = useDispatch();
  const { values, errors, valid } = useSelector((state) => state.auth);
  const email = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

  const validate = (name, value) => {
    function resetError() {
      dispatch({
        type: SET_ERRORS,
        payload: {
          ...errors,
          [name]: "",
        },
      });
    }
    switch (name) {
      case "name": {
        if (value.length < 2) {
          dispatch({
            type: SET_ERRORS,
            payload: {
              ...errors,
              [name]: "     2- ",
            },
          });
          return false;
        } else {
          resetError();
          return true;
        }
      }
      case "email": {
        if (!email.test(String(value))) {
          dispatch({
            type: SET_ERRORS,
            payload: {
              ...errors,
              [name]: "  email",
            },
          });
          return false;
        } else {
          resetError();
          return true;
        }
      }
      case "password": {
        if (value.length < 6 || value.length > 20) {
          dispatch({
            type: SET_ERRORS,
            payload: {
              ...errors,
              [name]: "    6  20 ",
            },
          });
          return false;
        } else {
          resetError();
          return true;
        }
      }
      default:
        break;
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    dispatch({
      type: SET_VALUES,
      payload: { ...values, [name]: value },
    });
    dispatch({
      type: SET_VALID,
      payload: { ...valid, [name]: validate(name, value) },
    });
  };

  const resetForm = useCallback(
    (
      newValues = { email: "", password: "" },
      newErrors = { email: "", password: "", submit: "" },
      newIsValid = { email: false, password: false }
    ) => {
      dispatch({
        type: SET_ERRORS,
        payload: newErrors,
      });
      dispatch({
        type: SET_VALUES,
        payload: newValues,
      });
      dispatch({
        type: SET_VALID,
        payload: newIsValid,
      });
    },
    [dispatch]
  );

  return {
    handleChange,
    resetForm,
  };
}
